"use client";

import React, { useEffect, useState } from "react";
import { auth, db, storage } from "../../firebaseConfigs/firebaseConfig";
import { doc, getDoc, updateDoc, enableNetwork } from "firebase/firestore";
import { ref, uploadBytes, getDownloadURL } from "firebase/storage";
import { useRouter } from "next/navigation";
import { onAuthStateChanged } from "firebase/auth";

export default function EditProfile() {
  const router = useRouter();
  const [user, setUser] = useState(null);
  const [profile, setProfile] = useState({
    name: "",
    offerSkills: "",
    wantSkills: "",
    profilePic: "",
  });
  const [uploading, setUploading] = useState(false);

  // 🔌 Ensure Firestore is online before any calls
  useEffect(() => {
    enableNetwork(db)
      .then(() => console.log("✅ Firestore network enabled"))
      .catch((err) => console.error("❌ Failed to enable network:", err));
  }, []);

  // 👤 Get current user
  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, async (currentUser) => {
      if (currentUser) {
        setUser(currentUser);

        const docRef = doc(db, "users", currentUser.uid);
        const docSnap = await getDoc(docRef);

        if (docSnap.exists()) {
          setProfile(docSnap.data());
        }
      } else {
        router.push("/login");
      }
    });

    return () => unsubscribe();
  }, []);

  // 📤 Handle profile image upload
  const handleImageUpload = async (e) => {
    const file = e.target.files[0];
    if (!file || !user) return;

    setUploading(true);
    const storageRef = ref(storage, `profilePics/${user.uid}`);
    await uploadBytes(storageRef, file);

    const downloadURL = await getDownloadURL(storageRef);
    setProfile((prev) => ({ ...prev, profilePic: downloadURL }));
    setUploading(false);
  };

  // ✅ Save profile to Firestore
  const handleSave = async () => {
    if (!user) return;

    const userRef = doc(db, "users", user.uid);
    await updateDoc(userRef, {
      name: profile.name,
      offerSkills: profile.offerSkills,
      wantSkills: profile.wantSkills,
      profilePic: profile.profilePic,
    });

    alert("Profile updated!");
    router.push("/dashboard");
  };

  return (
    <div className="p-4 max-w-xl mx-auto">
      <h1 className="text-2xl font-semibold mb-4">Edit Profile</h1>

      <input
        type="text"
        placeholder="Your name"
        value={profile.name}
        onChange={(e) => setProfile({ ...profile, name: e.target.value })}
        className="border p-2 mb-2 w-full"
      />

      <input
        type="text"
        placeholder="Skills you offer"
        value={profile.offerSkills}
        onChange={(e) => setProfile({ ...profile, offerSkills: e.target.value })}
        className="border p-2 mb-2 w-full"
      />

      <input
        type="text"
        placeholder="Skills you want to learn"
        value={profile.wantSkills}
        onChange={(e) => setProfile({ ...profile, wantSkills: e.target.value })}
        className="border p-2 mb-4 w-full"
      />

      <div className="mb-4">
        {profile.profilePic && (
          <img src={profile.profilePic} alt="Profile" className="w-24 h-24 rounded-full mb-2" />
        )}
        <input type="file" onChange={handleImageUpload} className="mb-2" />
        {uploading && <p>Uploading image...</p>}
      </div>

      <button onClick={handleSave} className="bg-blue-500 text-white px-4 py-2 rounded">
        Save Profile
      </button>
    </div>
  );
}
